import React, { useEffect, useState } from "react";
import styles from "./styles.module.css";
import axios from "../../axios";
import Button from "../Button";
import Table from "../Table";
import { Draggable } from "react-beautiful-dnd";
function InvoiceSetup(props) {
  // const [linkTemp, setLinkTemp] = useState([]);
  const [invoiceSettings, setInvoiceSettings] = useState([]);
  const [panelCahnge, setPanelChange] = useState("main");
  const [bank, setBank] = useState("");
  const [noRek, setNoRek] = useState("");
  const [namaRek, setNamaRek] = useState("");
  const [linkImg, setLinkImg] = useState("");
  const [fileName, setFileName] = useState("");
  const [img, setImg] = useState(new FormData());
  const [refreshRender, setRefreshRender] = useState(false);
  const [showTambahData, setShowTambahData] = useState(false);
  const [editButtonPressed, setEditButtonPressed] = useState(false);
  const [addButtonPressed, setAddButtonPressed] = useState(false);
  const [dataSelectedNow, setDataSelectedNow] = useState(null);

  const base_url = "https://fisikamu.space";

  useEffect(() => {
    async function fetchdata() {
      getInvoiceSettings();
    }

    fetchdata();

    if (refreshRender) {
      getInvoiceSettings();
      setRefreshRender(false);
    }
    if (addButtonPressed) {
      setBank("");
      setLinkImg("");
      setAddButtonPressed(false);
    }
  }, [refreshRender, addButtonPressed, editButtonPressed]);

  /// action function

  const getInvoiceSettings = () => {
    axios.get("/invoice_settings").then(function (response) {
      setInvoiceSettings(response.data);
    });
  };

  const onImageChange = (event) => {
    let files = event.target.files;
    const extLength = files[0]["name"].split(".").length;
    const dateCode = Date.now();
    const ext = files[0]["name"].split(".")[extLength - 1];
    const file_name = "slide_" + dateCode + "." + ext;
    setFileName(file_name);
    if (FileReader && files && files.length) {
      img.append("name", "slide_" + dateCode);
      img.append("name_before", "slide_" + dateCode);
      img.append("file", files[0]);
    }
  };

  const kembali = () => {
    setPanelChange("main");
    setAddButtonPressed(true);
    setEditButtonPressed(false);
    setShowTambahData(false);
  };

  const editInvoice = (item) => {
    setShowTambahData(true);
    setBank(item.bank);
    setNoRek(item.no_rek);
    setNamaRek(item.nama_rek);
    setDataSelectedNow(item);
  };
  const konfirmasi = (item) => {
    const data = {
      id: item.id,
      id_user: item.id_user,
      id_paket: item.id_paket,
      payment_type: item.payment_type,
      jumlah: item.jumlah,
      status: "Aktif",
      description: item.description,
    };
    // console.log(data);

    axios.put("/transaction_history", data).then(function (response) {
      // console.log(response);
      if (response.status == 200) {
        setShowTambahData(false);
        setRefreshRender(true);
      }
    });
  };

  const save = () => {
    if (img !== null && bank !== "") {
      const link_img = base_url + "/uploads/" + fileName;
      const data = {
        bank: bank,
        img: link_img,
      };
      // console.log(data);
      axios.post("/slide_content", data).then(function (response) {
        console.log(response);
      });

      axios.post("/file_upload", img).then(function (response) {
        console.log(response);
      });
      setRefreshRender(true);
      setShowTambahData(false);
      // setShowPanelInput(showPanelInput => !showPanelInput);
      // setRefreshRender(false);
    } else {
      alert("Isi Semua Data");
    }
  };

  const hapusData = (dataTemp) => {
    let data_delete = require("querystring");
    data_delete = data_delete.stringify({
      id: dataTemp.id,
    });

    axios
      .delete("/slide_content", {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
        data: data_delete,
      })
      .then(function (response) {
        console.log(response);
        if (response.status === 201) {
          setPanelChange("main");
          setRefreshRender(true);
        }
      });
  };

  const editData = (data) => {
    setEditButtonPressed(true);
    setBank(data.bank);
    setLinkImg(data.img);
    setDataSelectedNow(data);
    setShowTambahData(true);
  };

  const dataEdited = () => {
    const data = {
      id: dataSelectedNow.id,
      bank: bank,
      no_rek: noRek,
      nama_rek: namaRek,
    };

    axios.put("/invoice_settings", data).then(function (response) {
      console.log(response);
      if (response.status == 200) {
        setBank("");
        setNoRek("");
        setNamaRek("");
        setShowTambahData(false);
        setRefreshRender(true);
      }
    });
  };

  // panel components

  const mainPanel = (
    <>
      {/* <Table
                dropID = {"drop-1"}
                thList={["no", "Nama","Deskripsi","Nama Paket","Harga","Status", "Action"]}
                tdList={invoiceSettings.map((item,index) => (
                    <tr key={item.id}>
                        <td>{index+1}</td>
                        <td>{item.username}</td>
                        <td>{item.description}</td>
                        <td>{item.nama}</td>
                        <td>{item.jumlah}</td>
                        <td>{item.status}</td>
                        <td><a href="#0" onClick={()=>konfirmasi(item)}>Konfirmasi</a></td>
                    </tr>
                    ))}
            /> */}
      <br />
      {invoiceSettings.map((item, index) => (
        <div>
          <p>Bank : {item.bank}</p>
          <p>Nomor Rekening :{item.no_rek}</p>
          <p>Nama Rekening :{item.nama_rek}</p>
          <Button link='' title='Edit' onClick={() => editInvoice(item)} />
        </div>
      ))}
    </>
  );

  const panelTambahData = (
    <>
      <h1>Edit Invoice</h1>
      <input
        value={bank}
        type='text'
        placeholder='Bank'
        onChange={(e) => {
          setBank(e.target.value);
        }}
      />
      <br />
      <input
        value={noRek}
        type='number'
        placeholder='Nomor Rekening'
        onChange={(e) => {
          setNoRek(e.target.value);
        }}
      />
      <br />
      <input
        value={namaRek}
        type='text'
        placeholder='Nama Rekening'
        onChange={(e) => {
          setNamaRek(e.target.value);
        }}
      />
      <br />

      <br />

      <Button link='' title='Submit' onClick={() => dataEdited()} />
      <Button link='' title='Kembali' onClick={() => kembali()} />
    </>
  );

  return (
    <>
      <div className={styles.container}>
        {panelCahnge === "main" ? mainPanel : null}
      </div>
      {showTambahData ? (
        <div className={styles.tambah_soal}>
          <div className={styles.layer1}>{panelTambahData}</div>
        </div>
      ) : null}
    </>
  );
}

export default InvoiceSetup;
